<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process167"></a>

<p><br></p>
<p><span class="rvts17">浮木阴影（</span><span class="rvts50">Shadow over Driftwood </span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:404,Y:759</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：洛哈（</span><span class="rvts20">Lohar</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找出洛哈的手下莫德斯（</span><span class="rvts20">Mordus</span><span class="rvts21">）的下落</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1148.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">矮人黑恶势力洛哈位于浮木城酒馆的下层，距离竞技场不远的一处拐角里。洛哈是典型的反派人物，语言较为苛刻，手段较为恶毒。</span>
<br><span class="rvts21">在洛哈房间深处的一间锁着的牢房里，有几名大麻的种植者。因此洛哈的罪名不仅仅只有走私，勒索（他是</span><span class="rvts20"></span><span class="rvts21">爱情有代价</span><span class="rvts20"></span><span class="rvts21">支线中的幕后老板），还有谋杀和贩毒。</span>
<br><span class="rvts21">即便是这样，城中的魔导师士兵对他的罪行都是睁只眼闭只眼（当你发现洛哈这里有毒品并举报时，他们会表现的很冷淡），或许是因为胆怯，或许是因为同僚。</span>
<br><span class="rvts21">在这里首先会接到他浮木阴影的任务。洛哈想要找回失踪的矮人。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1149.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">失踪的矮人其实还在浮木城里。在浮木城西门附近的一间破旧屋子里，有一个矮人正在睡觉，尝试叫醒她，让她挪开屁股下面的地下室障碍物。然后进入地下室进行调查。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1150.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">地下室里的一处墙壁处有一个开关，使用它，可以打开一扇石门。里面存在着被困的两个矮人。矮人是洛哈的手下，救出他们后，便可以升级该支线任务。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1151.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">进入调查洞穴，会发现莫德斯想要刺杀洛哈的信件。可以看出，莫德斯也想要推翻洛哈的统治。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1152.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在墙上的一个指挥板上，可以查看最高通缉，来获得支线任务的更新，得知莫德斯或许藏到了失事洞穴里面。</span>
<br><span class="rvts21">另外该房间存在一个石板机关，触碰它会使石门关闭。需要使一名角色压住石板机关，另外一人寻找墙上的一处开关，即可再次打开石门。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1153.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在该地下室中还可以找到一处骷髅机关，该骷髅机关可以引出支线</span><span class="rvts20"></span><span class="rvts21">自由的味道</span><span class="rvts20"></span><span class="rvts21">。目前为止，暂时无法打开它（除非开锁等级达到</span><span class="rvts20">5</span><span class="rvts21">级）。详情参考相关支线</span><span class="rvts20"></span><span class="rvts21">自由的味道</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1154.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">接着，需要探索失事洞穴（详见失事洞穴的探索篇），在这里杀出一条血路，找到莫德斯。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1155.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">可以在一个矮人炼金师的房间找到一些升级任务的日记以及信件。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1156.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">最终，莫德斯位于一处船体残骸的上层位置。爬这里的楼梯，准备与莫德斯进行交战。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1157.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">与莫德斯的战斗中，务必要记得最简单的打发。那就是尽可能不要使它变形（他变形的条件是他的士兵有人死亡，并且我们使用了</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">，莫德斯吸取了死亡灵魂的源力）。</span>
<br><span class="rvts21">换句话说，只要击败莫德斯，其余的士兵会瞬间死亡，结束战斗。但是如果可以杀死变形后的莫德斯，可以获得额外的经验。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1158.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">与莫德斯对话，并放过他，他会教你额外的一点源力值。如果一直询问他内心一直恐惧的人是谁，（也就是他的主人是谁），他会被莫名的扯掉皮囊，我们将不会从他那里学习到额外的源力技能点。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1159.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">当击败莫德斯后，在他的房间里会发现一封矮人女王的信，该信件十分重要，将影响着</span><span class="rvts20"></span><span class="rvts21">浮木阴影</span><span class="rvts20"></span><span class="rvts21">以及</span><span class="rvts20"></span><span class="rvts21">教会的法律</span><span class="rvts20"></span><span class="rvts21">的结局。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1160.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">最后返回浮木城，报告魔导师朱利安，莫德斯被击败，并把相关的物证（矮人信件，大块污迹信）上交给他，朱利安会命令立马围捕洛哈。</span>
<br><span class="rvts21">此时朱利安会给一把兵营二楼的钥匙，里面会有一些较好的战斗装备。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1161.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">与洛哈发生打斗，这场战斗将会十分艰难。洛哈是一个比较难对付的矮人。尽可能不要让他逃到大麻种植区，引起里面额外的士兵支援。</span>
<br><span class="rvts21">另外，在取得胜利后，如果在之前的</span><span class="rvts20"></span><span class="rvts21">爱的代价</span><span class="rvts20"></span><span class="rvts21">支线中，身上的所有装备被劫走，可以在洛哈这里的一个木架上的背包处找到。</span>
<br><span class="rvts21">杀死洛哈后，找到魔导师朱利安，进行领赏。到此为止，</span><span class="rvts20"></span><span class="rvts21">浮木阴影</span><span class="rvts20"></span><span class="rvts21">以及</span><span class="rvts20"></span><span class="rvts21">教会的法律</span><span class="rvts20"></span><span class="rvts21">支线将会全部以好的结局方式关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
